<template>
	<b-container class="bv-example-row">
		<div class="header"></div>
		<div>
			<b-jumbotron>
				<template #header>Quiz</template>

				<template #lead>
					<div class="number">Question: {{ index + 1 }}</div>
					<div class="count">Correct: {{ correct }}/{{ total }}</div>
				</template>

				<hr class="my-4" />

				<b-row>
					<b-col sm="6" offset="3">
						<div class="question-box">
							<p v-if="questions[index]" v-html="questions[index].question"></p>

							<b-list-group>
								<b-list-group-item
									v-for="(answer, index) in answers"
									:key="index"
									@click="selectedAnswer(index)"
									:class="selectClass(index)"
									:disabled="currectIndex !== null"
								>
									{{ answer }}
								</b-list-group-item>
							</b-list-group>

							<b-button
								variant="primary"
								class="submit"
								href="#"
								@click.prevent="submitAnswer"
								:disabled="selectedIndex === null || currectIndex !== null"
								>Submit</b-button
							>
							<b-button
								variant="success"
								href="#"
								@click.prevent="nextQuestion"
								:disabled="questions.length < index"
								>Next</b-button
							>
						</div>
					</b-col>
				</b-row>
			</b-jumbotron>
		</div>
	</b-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import _ from "lodash";

export default {
	name: "QuestionBox",
	data() {
		return {
			index: 0,
			correct: 0,
			total: 0,
			selectedIndex: null,
			currectIndex: null,
		};
	},
	watch: {},
	methods: {
		...mapActions(["fetchQuestions"]),
		nextQuestion() {
			if (this.questions.length - 1 > this.index) {
				this.index = parseInt(this.index) + 1;
			}
			this.selectedIndex = null;
			this.currectIndex = null;
		},
		selectedAnswer(index) {
			this.selectedIndex = index;
		},
		submitAnswer() {
			const correct = this.answers.indexOf(
				this.questions[this.index].correct_answer
			);
			this.currectIndex = correct;
			this.total++;
			if (this.currectIndex === this.selectedIndex) {
				this.correct++;
			}
		},
		selectClass(index) {
			if (this.currectIndex === null && this.selectedIndex === index) {
				return "selected";
			} else if (this.currectIndex !== null && index === this.currectIndex) {
				return "correct";
			} else if (this.selectedIndex === index && index !== this.currectIndex) {
				return "incorrect";
			} else {
				return "";
			}
		},
	},
	computed: {
		...mapGetters(["questions"]),
		answers: function() {
			let answers = this.questions[this.index].incorrect_answers;
			answers.push(this.questions[this.index].correct_answer);
			answers = _.shuffle(answers);
			return answers;
		},
	},
	mounted() {
		this.fetchQuestions();
	},
};
</script>

<style scoped>
.lead {
	display: flex;
	justify-content: space-between;
}
hr {
	margin-top: 0px !important;
}
.list-group {
	margin-bottom: 1rem;
}
.list-group-item:hover {
	background: #eee;
	cursor: pointer;
}
/* .list-group-item.disabled,
.list-group-item:disabled {
	background: unset;
} */
.selected,
.selected:hover {
	background: lightblue;
}
.correct,
.correct:hover {
	background: lightgreen !important;
}
.incorrect,
.incorrect:hover {
	background: lightcoral !important;
}
.submit {
	margin-right: 10px;
}
</style>
